.app-wrapper {
  width: 1200px;
  margin: 0 auto;
  margin-top: 10px;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
  grid-template-areas:
          'header header'
          'nav content';
}
.app-content {
  grid-area: content;
  margin: 20px 0 0 20px;
}



